// media query mixin - max-width
@mixin respond-max($breakpoint) {
  @media only screen and (max-width: $breakpoint) {
    @content;
  }
}
// media query mixin - min-width
@mixin respond-min($breakpoint) {
  @media only screen and (min-width: $breakpoint) {
    @content;
  }
}

// theme mixin
@mixin theme($light: true) {
  $primary-color: if($light, #fff, #000);
  $secondary-color: if($light, #bebebe, red);
  $text-color: if($light, #000, #fff);

  @content;
}
