.projects-section {
  @include svg;
  padding: 14rem 6rem;
  text-align: center;
  position: relative;

  @include respond-max($largest-1200px) {
    padding: 8rem;
  }

  @include respond-max($small-420px) {
    padding: 6rem 0 0 0;
  }

  .primary-heading {
    margin-bottom: 6rem;

    @include respond-max($small-420px) {
      padding: 0 6rem;
    }
  }

  .projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    &__content {
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.938);
      width: 100%;
      padding: 2rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      transform: translateY(-100%);
      transition: all 0.2s;
    }

    &__card {
      position: relative;
      height: 25rem;
      overflow: hidden;
      // opacity: 0;
      border: 1px solid rgb(224, 224, 224);
      margin: 1rem;

      width: 39rem;

      @include respond-max($large-650px) {
        margin: 0;
      }

      @include respond-max($small-420px) {
        width: 100%;
      }

      & > *:not(:last-child) {
        margin-bottom: 5px;
      }
      &:hover .projects__content {
        transform: translateY(0);
      }
    }

    &__img {
      width: 100%;
      height: 100%;
    }

    &__title {
      font-size: 2.5rem;
      font-weight: 600;
    }

    &__desc {
      font-size: 1.5rem;
    }

    &__skills {
      font-size: 1.4rem;
      color: rgb(241, 105, 105);
    }

    &__link {
      padding: 1rem;
      background-color: #000;
      color: #fff;
      font-size: 1.2rem;
      text-decoration: none;
      border-radius: 5px;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        width: 2rem;
        height: 2rem;
        margin-right: 1rem;
      }
    }
  }

  .toggle__btn {
    padding: 5px 10px;
    color: white;
    border: none;
  }
}

.modal-project {
  padding: 2rem;
  display: flex;
  height: 100%;

  & > * {
    flex: 1;
  }

  &__media {
    height: 100%;
    overflow: hidden;
    padding-top: 2rem;
    display: flex;
  }

  .project__video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content {
    padding: 3rem;
  }

  &__title {
    font-size: 5rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
  }

  &__desc {
    font-size: 1.6rem;
  }

  &__poster {
    height: 100%;
    margin: 0 auto;
  }
}
.cancel-icon {
  position: absolute;
  z-index: 100;
  top: 2rem;
  right: 2rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
